<template>
  <div>
    <van-nav-bar
      :title="title"
      left-text=""
      left-arrow
      @click-left="backHandler"
      fixed
      placeholder
    />
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text=""
      @load="onLoad"
    >
      <van-card
        v-for="item in list"
        :key="item.key"
        :price="item.price"
        :desc="title"
        :title="item.trainNumber"
        thumb="https://blog.geohey.com/content/images/2018/04/high-speed-train.jpg"
        @click="cardClickHandler(item)"
      >
        <template #desc>
          <p>{{ title }}</p>
        </template>
      </van-card>
      <van-sku
        :show-add-cart-btn="false"
        v-model="showSku"
        :goods-id="selectedTrainNumber"
        :sku="sku"
        :goods="goods"
        :quota="1"
        @buy-clicked="buyClickHandler"
      >
        <template #sku-header-extra>
          {{ selectedTrainNumber }}
        </template>
      </van-sku>
    </van-list>
  </div>
</template>

<script>
import { NavBar, List, Cell, Card, Sku } from "vant";

export default {
  components: {
    [NavBar.name]: NavBar,
    [List.name]: List,
    [Cell.name]: Cell,
    [Card.name]: Card,
    [Sku.name]: Sku,
  },
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      showSku: false,
      selectedTrainNumber: "",
      sku: {
        // sku
        // 
        tree: [
          {
            k: "", // skuKeyName
            k_s: "s1", // skuKeyStrsku  list key value  id
            v: [
              {
                id: "1", // skuValueId id
                name: "A", // skuValueName
                imgUrl: require("../../assets/img/seat.png"), // 
                previewImgUrl: require("../../assets/img/seat.png"), // 
              },
              {
                id: "2",
                name: "B",
                imgUrl: require("../../assets/img/seat.png"), // 
                previewImgUrl: require("../../assets/img/seat.png"), // 
              },
              {
                id: "3",
                name: "C",
                imgUrl: require("../../assets/img/seat.png"), // 
                previewImgUrl: require("../../assets/img/seat.png"), // 
              },
              {
                id: "4",
                name: "D",
                imgUrl: require("../../assets/img/seat.png"), // 
                previewImgUrl: require("../../assets/img/seat.png"), // 
              },
              {
                id: "5",
                name: "F",
                imgUrl: require("../../assets/img/seat.png"), // 
                previewImgUrl: require("../../assets/img/seat.png"), // 
              },
            ],
            largeImageMode: true, //  
          },
        ],
        //  sku M  sku S 
        list: [
          {
            id: 1, // skuId
            s1: "1", //  k_s  s1  id
            price: (Math.random() * 80000 + 2000).toFixed(2), // 
            stock_num: 110, //  sku 
          },
          {
            id: 2, // skuId
            s1: "2", //  k_s  s1  id
            price: (Math.random() * 80000 + 2000).toFixed(2), // 
            stock_num: 110, //  sku 
          },
          {
            id: 3, // skuId
            s1: "3", //  k_s  s1  id
            price: (Math.random() * 80000 + 2000).toFixed(2), // 
            stock_num: 110, //  sku 
          },
          {
            id: 4, // skuId
            s1: "4", //  k_s  s1  id
            price: (Math.random() * 80000 + 2000).toFixed(2), // 
            stock_num: 110, //  sku 
          },
          {
            id: 5, // skuId
            s1: "5", //  k_s  s1  id
            price: (Math.random() * 80000 + 2000).toFixed(2), // 
            stock_num: 110, //  sku 
          },
        ],
        price: (Math.random() * 800 + 20).toFixed(2), // 
        stock_num: 227, // 
        collection_id: 2261, //  skuId  collection_id sku  id
        none_sku: false, // 
        hide_stock: false, // 
      },
      goods: {
        //  sku 
        picture:
          "https://blog.geohey.com/content/images/2018/04/high-speed-train.jpg",
      },
    };
  },
  computed: {
    title() {
      const { from, to, date } = this.$route.query;
      return `${from} ->
      ${to}
      ${date}`;
    },
  },
  methods: {
    backHandler() {
      this.$router.go(-1);
    },
    cardClickHandler(item) {
      const { trainNumber } = item;
      this.selectedTrainNumber = trainNumber;
      this.showSku = true;
    },
    onLoad() {
      // 
      // setTimeout  ajax 
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push({
            key: this.list.length + 1,
            price: (Math.random() * 800 + 20).toFixed(2),
            trainNumber: `${
              (Math.random() * 10).toFixed(0) % 2 === 0 ? "D" : "G"
            }${(Math.random() * 9001 + 999).toFixed(0)}`,
          });
        }

        // 
        this.loading = false;

        // 
        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 1000);
    },
    buyClickHandler(sku) {
      const {
        selectedSkuComb: { id },
      } = sku;
      const { selectedTrainNumber } = this;
      const { date } = this.$route.query;
      this.$router.push({
        path: "/order",
        query: {
          trainNumber: selectedTrainNumber,
          seatId: id,
          date,
        },
      });
    },
  },
};
</script>

<style lang="less">
.user {
  &-poster {
    width: 100%;
    height: 53vw;
    display: block;
  }

  &-group {
    margin-bottom: 15px;
  }

  &-links {
    padding: 15px 0;
    font-size: 12px;
    text-align: center;
    background-color: #fff;

    .van-icon {
      display: block;
      font-size: 24px;
    }
  }
}
</style>
